require('./themes/minimal.css')

exports.onInitialClientRender = async (_, { theme, color, cdn ,...paceOptions }) => {


  const colors = document.createElement('style')
  colors.innerHTML = `
    body {
      --pace-progress-color: ${color}
    }
  `




  
  document.head.appendChild(colors)


  // require(`./themes/${theme || 'minimal'}.css`)

  if (cdn) {
    const sc = document.createElement('script')
    sc.dataset.paceOptions = JSON.stringify(paceOptions)
    sc.src = cdn
    document.body.appendChild(sc)
  } else {
    // const md = await import('./pace.min.js')
    // const pace = md.default
    // pace.start(paceOptions)
  }
}
